<template>
  <div class="role">
    <!--头部-->
    <div>
      <CommonHeader></CommonHeader>
    </div>
    <!--tab导航部分-->
    <div class="fix">
      <Set></Set>
    </div>

    <!-- 角色内容部分 -->
    <div class="P-content">
      <div class='P-c-header'>
        <button type="button" class="btn btn-default pull-right" @click='addRoles'><i class="fa fa-plus"></i> 新增</button>
        <div class='P-c-h-Text'>角色管理</div>
        <span class='border-cover'></span>
      </div>
      <div class="P-content-top">
        <div class='P-c-t-bottom fix'>
          <input type="text" v-model='param.name' class="form-control P-t-inputBox3" style='width:200px; margin:0;display: inline;'
                 placeholder="请输入角色名称" @keyup.enter="getRoles"/>
          <button type="button" class='btn btn-primary searchBtn' @click='getRoles'>搜索</button>
        </div>
      </div>

      <div class='P-content-bottom'>
        <table class="table table-bordered table-hover">
          <thead>
            <tr>
              <th>角色名称</th>
              <th>创建时间</th>
              <th>角色描述</th>
              <th>操作</th>
            </tr>
          </thead>

          <tbody>
            <tr v-for="item in roleList">
              <td>{{item.name}}</td>
              <td>{{item.createdWhen | dataFilter}}</td>
              <td>{{item.description}}</td>
              <td>
                <router-link :to='"/RoleDetail/"+item.id'>
                  <button type="button" class="btn btn-primary btn-sm details-btn"><i class="fa fa-edit"></i> 编辑</button>
                </router-link>
              </td>
            </tr>
            <tr v-if="roleList.length == 0">
              <td colspan="4">
                <span>暂无数据</span>
              </td>
            </tr>
          </tbody>
        </table>
      </div>

    </div>
  </div>
</template>

<style>
  /*整个快得样式*/
  .P-content{
    margin:50px 15px 0px 195px;
    min-width:960px;
  }
  /*上部分样式*/
  .P-content .P-c-header{
    padding:8px 0px;
    font-size:16px;
    height:50px;
    background:white;
    position:relative;
    border-bottom:1px solid #ddd;
  }
  .P-c-h-Text{
    padding:8px 8px 8px 8px;
  }
  .P-c-header .border-cover{
    display:block;
    width:1px;
    height:14px;
    background:#6886d4;
    position:absolute;
    left:0px;
    top:20px;
  }
  .P-content .P-content-top{
    margin-top:15px;
    margin-bottom:15px;
  }
  .P-content-top .P-c-t-bottom {
    margin-top:15px;
  }
  .P-content-top .P-c-t-bottom select{
    height: 34px;
    width: 100px;
    border: solid 1px #ccc;
    border-radius: 4px;
    box-shadow: inset 0 1px 1px rgba(0,0,0,.075);
  }
  .P-t-inputBox1,.P-t-inputBox2,.P-t-inputBox3{
    display:inline-block;
  }
  /*下部分表格样式*/
  .P-content-bottom table th,.P-content-bottom table{
    text-align:center;
  }
  .P-content-bottom .table thead{
    background:#f5f5f5;
  }
  /*table样式*/
  .P-content-bottom .table tr{
    text-align:center;
  }
  .P-content-bottom .table td{
    padding:4px 0px;
    line-height:39px;
  }
  .P-content-bottom .table th{
    text-align:center;
    border-bottom:none;
  }
  .P-c-t-bottom .searchBtn {
    margin-left: 10px;
  }
</style>

<script >
  //header
  import CommonHeader from '../public/header.vue';
  //menu
  import Set from '../public/set.vue';
  import moment from 'moment';
  import $ from 'jquery';

  export default {
    name: "Role",
    data: function() {
      return {
        param: {
          name: "",
        },
        roleList: [],
      }
    },
    mounted:function(){
      this.getRoles();//查询
    },
    methods : {
      getRoles: function () {
        var url = "/api/uaa/roles/selectName";
        var token = localStorage.getItem('access_token');
        this.$http.get(url, {
          headers: {
            Authorization: 'bearer ' + token
          },
          params:this.param
        }).then(function (res) {
          //赋值
          this.roleList=res.body;

        },function (err) {
          console.log(err);
        })
      },
      //新增角色
      addRoles: function () {
        this.$router.push({name:'AddRole'});
      }
    },
    components: {
      CommonHeader: CommonHeader,
      Set: Set
    }
  }
</script>



